import { useState, useEffect, useRef } from 'react';
import './Clock.css';

function Clock(props) {
	const [seconds, setSeconds] = useState(0);
	const [minutes, setMinutes] = useState(2);

	const [clockTime, setClockTime] = useState(
		String(minutes).padStart(2, '0').concat(':').concat('00')
	);
	const [breakLength, setBreakLength] = useState(1);
	const [sessionLength, setSessionLength] = useState(2);
	const [clockLabel, setClockLabel] = useState('Session');

	const [playTimerSelected, setPlayTimerSelected] = useState(true);
	const [canSelectBreakSession, setCanSelectBreakSession] = useState(true);

	const [breakFinished, setBreakFinished] = useState(false);

	const timer = useRef();

	const handleReset = () => {
		clearTimeout(timer.current);
		setSeconds(0);
		setMinutes(25);
		setBreakLength(5);
		setSessionLength(25);
		setPlayTimerSelected(true);
		setCanSelectBreakSession(true);
		setClockLabel('Session');
		setBreakFinished(false);
	};

	useEffect(() => {
		////console.log(seconds);
		timer.current = setTimeout(() => {
			//play pause button should pause time
			if (playTimerSelected !== true) {
				if (minutes > 0) {
					//console.log('line 40 :' + clockLabel);
					//if seconds has hit zero
					if (seconds > 0) {
						console.log('line 43 :' + clockLabel);
						setSeconds(seconds - 1);
					} else if (seconds === 0) {
						console.log('line 46 :' + clockLabel);
						setMinutes(minutes - 1);
						setSeconds(59);
					}
				} else if (minutes === 0) {
					//you can change color of clock value here ??
					if (seconds > 0) {
						console.log('line 64 :' + clockLabel);
						setSeconds(seconds - 1);
					} else if (seconds === 0) {
						console.log('line 67 :' + clockLabel);
						if (breakFinished) {
							console.log('line 69 :' + clockLabel);
							setClockLabel('Session');
							setMinutes(sessionLength);
						} else {
							console.log('line 73 :' + clockLabel);
							setClockLabel('Break');
							setMinutes(breakLength);
						}
						clearTimeout(timer.current);
						setBreakFinished(!breakFinished);
						setSeconds(0);
					}
				}
			}
			//minutes is zero
		}, 500);
		return () => {
			clearTimeout(timer.current);
		};
	}, [
		minutes,
		seconds,
		playTimerSelected,
		breakLength,
		breakFinished,
		sessionLength,
		clockLabel,
	]);

	useEffect(() => {
		setClockTime(
			String(minutes)
				.padStart(2, '0')
				.concat(':')
				.concat(String(seconds).padStart(2, '0'))
		);
	}, [minutes, seconds]);

	const handleStartStop = () => {
		setPlayTimerSelected(!playTimerSelected);
		setCanSelectBreakSession(!canSelectBreakSession);
	};

	const handleBreakIncrement = () => {
		if (canSelectBreakSession === true) {
			if (breakLength === 60) {
				//do nothing
			} else {
				setBreakLength(breakLength + 1);
			}
		}
	};

	const handleBreakDecrement = () => {
		if (canSelectBreakSession === true) {
			if (breakLength > 1) {
				setBreakLength(breakLength - 1);
			}
		}
	};

	const handleSessionIncrement = () => {
		if (canSelectBreakSession === true) {
			if (sessionLength === 60) {
				//do nothing
			} else {
				setMinutes(sessionLength + 1);
				setSessionLength(sessionLength + 1);
			}
		}
	};

	const handleSessionDecrement = () => {
		if (canSelectBreakSession === true) {
			if (sessionLength > 1) {
				setMinutes(sessionLength - 1);
				setSessionLength(sessionLength - 1);
			}
		}
	};

	return (
		<div className='MainContainer bg-success p-5 d-flex flex-column justify-content-center align-items-center'>
			<div className='AppTitleDiv pt-3 pb-3 mb-3'>
				<h1 className='AppTitle'>25 + 5 Clock</h1>
			</div>
			<div className='ControlDiv d-flex flex-row justify-content-center'>
				<div className='ControlSection p-3 m-2 rounded-3'>
					<div className='ControlTitle'>
						<label id='break-label' className='h2 text-nowrap'>
							Break Length
						</label>
					</div>
					<div className='Controls d-flex justify-content-center align-items-center'>
						<i
							id='break-increment'
							className='fa fa-arrow-circle-up fa-3x m-2'
							aria-hidden='true'
							onClick={handleBreakIncrement}></i>
						<label id='break-length' className='ControlValue h2 m-2'>
							{breakLength}
						</label>
						<i
							id='break-decrement'
							className='fa fa-arrow-circle-down fa-3x m-2'
							aria-hidden='true'
							onClick={handleBreakDecrement}></i>
					</div>
				</div>
				<div className='ControlSection p-3 m-2 rounded-3'>
					<div className='ControlTitle'>
						<label id='session-label' className='h2 text-nowrap'>
							Session Length
						</label>
					</div>
					<div className='Controls d-flex justify-content-center align-items-center'>
						<i
							id='session-increment'
							className='fa fa-arrow-circle-up fa-3x m-2'
							aria-hidden='true'
							onClick={handleSessionIncrement}></i>
						<label id='session-length' className='ControlValue h2 m-2'>
							{sessionLength}
						</label>
						<i
							id='session-decrement'
							className='fa fa-arrow-circle-down fa-3x m-2'
							aria-hidden='true'
							onClick={handleSessionDecrement}></i>
					</div>
				</div>
			</div>
			<div className='LiveClockDiv m-3 p-5 d-flex flex-column justify-content-around  align-items-center'>
				<label id='timer-label' className='h2'>
					{clockLabel}
				</label>
				<label id='time-left' className='ClockTime'>
					{clockTime}
				</label>
			</div>
			<div className='PausePlayReset pt-3 pb-3 mt-3 d-flex justify-content-center'>
				<div id='start_stop' className='me-5' onClick={handleStartStop}>
					<i
						className='fa fa-play-circle fa-3x mt-2 mb-2'
						aria-hidden='true'></i>
					<i
						className='fa fa-pause-circle fa-3x mt-2 mb-2'
						aria-hidden='true'></i>
				</div>
				<i
					id='reset'
					className='fa fa-refresh fa-3x m-2'
					aria-hidden='true'
					onClick={handleReset}></i>
			</div>
		</div>
	);
}
export default Clock;
